import React from "react"
import Image from "./images/mypic.jpg";
import btnEmail from "./images/EmailButton.png";
import LinkedButton from "./images/LinkedButton.png";
import insta from "./images/Instagram_Icon.png";
import facebook from "./images/Facebook_Icon.png";
import twitter from "./images/Twitter_Icon.png";
import github from "./images/GitHub_Icon.png";


export default function Card(props) {
    return (
        <div className="my-digital-card">
            
           <div><img src={Image} className="profile--image" alt="mypic"/></div> 
            
           <div className="info">
            <h2>{props.name}</h2>
            <h4>{props.mainTitle}</h4>
            <p>{props.Website}</p>
            </div>

            <div className="btn">
            
            <a href="mailto:mary.usaji@gmail.com"><img src={btnEmail}className="Email" alt="Email button"/></a>
            <a href="https://www.linkedin.com/in/mary-usaji-36961aa6/"><img src={LinkedButton} className="LinkedIn" alt="LinkedIn button"/></a>
            </div>

            <div className="About">
            <h3>{props.title}</h3>
            <p>{props.description}</p>

            </div>

            <div className="interest">
                
            <h3>{props.inttitle}</h3>
            <p>{props.intdescription}</p>
            </div>

            <div className="footer">            
            
            <a href="https://www.instagram.com/usajih/"><img src={insta} className="instagram" alt="Instagram Icon"/></a>
            <a href="https://github.com/Usajim"><img src={facebook} className="github" alt="Github Icon"/></a>
            <a href="https://m.facebook.com/public/Mary-Usaji/"><img src={twitter} className="facebook" alt="Facebook Icon"/></a>
            <a href="https://twitter.com/usajimary"><img src={github} className="twitter" alt="Twitter Icon"/></a>
            </div>

        
    </div>
    )
    
}

